.g-btn {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

.mat-form-field {
  width: 100%;
}

.mat-expansion-panel:not([class*="mat-elevation-z"]) {
  box-shadow: none;
}

.mat-expansion-panel {
  background: transparent;
  color: rgba(0, 0, 0, 0.87);
}

.mat-card {
  height: 100%;
}

.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):active,
.show>.btn-secondary.dropdown-toggle {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

.btn-secondary {
  min-width: 124px;
}

.btn-group-toggle .btn-secondary {
  color: #fff;
  background-color: #a1a1a1;
  border-color: transparent;
  border-radius: 5px !important;
  padding: 5px 8px;
  font-size: 13.5px;
  font-weight: 400;
}

.example-container {
  height: calc(100vh - 165px);
  padding: 12px;
  overflow: auto;
}

table {
  width: 100%;
}

.select-outer {
  float: right;
}

.heading {
  margin: 0;
  font-size: 22px;
  font-weight: 400;
}

.color1 {
  background: #0eab1c;
  height: 28px !important;
  display: inline-block;
  align-items: flex-end;
  vertical-align: middle;
  width: 5px;
  margin-right: 17px;
}

.completed {
  background: #0eab1c;
  height: 28px !important;
  display: inline-block;
  align-items: flex-end;
  vertical-align: middle;
  width: 5px;
  margin-right: 17px;
}

.pending {
  background: #969696;
  height: 28px !important;
  display: inline-block;
  align-items: flex-end;
  vertical-align: middle;
  width: 5px;
  margin-right: 17px;
}

.sort {
  width: 170px;
  float: right;
  font-family: "GothamNarrowBold", sans-serif;
}

.sort-text {
  min-width: 70px;
  font-size: 11px;
  margin-right: 5px;
  text-align: right;
}

.sort .mat-form-field-appearance-outline .mat-form-field-infix {
  padding: 1px 2px 6px 1px !important;
  font-size: 12px;
}

.sort .mat-raised-button.mat-warn {
  background-color: #fd6400;
}

.right-filter .mat-raised-button.mat-warn {
  box-shadow: 0 0 6px 1px rgba(255, 100, 0, 0.29) !important;
  background-color: #ff6400 !important;
  min-width: 161px;
}

.any-src {
  padding-right: 16px;
  text-align: left;
  white-space: nowrap;
}

.synch {
  font-size: 12px;
  margin-right: 15px;
}

.all {
  margin-left: 13px;
  font-weight: bold;
  font-size: 14px;
  font-family: "GothamNarrowBold", sans-serif;
}

.head1 {
  text-align: left !important;
  font-size: 17px;
}

.patient-name {
  font-weight: 400;
  color: #0075bc;
  font-family: "GothamNarrowBold", sans-serif;
}

.example-container table tr th:nth-of-type(2),
.example-container table tr td:nth-of-type(2).example-container table tr th:nth-of-type(3),
.example-container table tr td:nth-of-type(3) {
  min-width: 98px !important;
}

.imgouter {
  width: 42px;
  text-align: center;
}

.norecord {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
  border: 1px solid #ccc9;
  border-top: transparent;
  background-color: #fbfbfb;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
  padding-right: 12px;
}

.example-container tr td:last-child,
.example-container tr th:last-child {
  max-width: 150px;
  width: 100px;
  white-space: nowrap;
  text-align: left;
}

table tr th:first-child {
  border-radius: 5px 0 0 0px !important;
}

table tr th:last-child {
  border-radius: 0px 5px 0 0px !important;
}

.example-container1 {
  height: calc(100vh - 20px);
  position: relative;
  z-index: 0;
}

.example-sidenav {
  padding: 20px;
}

.dashboard-wrapper {
  padding-left: 0 !important;
}

.ipadfiler {
  display: none;
}

.example-sidenav {
  padding: 0;
}

.dashboard-wrapper {
  padding-left: 0 !important;
}

@media (max-width: 1025px) {
  .dashboard-wrapper {
    padding-left: 12px !important;
    padding-right: 12px !important;
  }

  .btn-secondary {
    min-width: auto;
  }

  .toprow {
    flex-direction: row;
    padding-right: 0 !important;
  }

  .ipadfiler {
    display: block;
  }

  .example-sidenav {
    padding: 0;
    top: 56px;
  }

  .mat-card {
    border-radius: 10px !important;
  }

  .ipadfiler button {
    min-width: 75px;
    padding: 0;
    margin-left: 0;
  }

  .sidefilter .head1 {
    padding-left: 8%;
  }
}

.ptpart .plist-date {
  font-size: 11px;
  word-break: break-all;
  white-space: normal;
  text-align: left;
  display: block;
  width: inherit;
}

td.mat-cell:first-of-type,
td.mat-footer-cell:first-of-type,
th.mat-header-cell:first-of-type {

  min-width: 106px !important;
  word-break: break-all !important;
}

.mat-row:hover {
  background-color: whitesmoke !important;
}

td.mat-cell {
  cursor: default !important;
}

i.filtertxt {
  color: #ED8B00 !important;
  font-style: normal;
  font-size: 16px;
  margin-left: 5px;

}

@media screen and (min-width:768px) {
  th.mat-header-cell {
    white-space: nowrap !important;
    padding-right: 23px !important;
  }
}

.m-card h5 {
  display: none;
}

.left-block {
  /* width: calc(96vw - 445px);   
  right: 300px; */
  width: calc(97vw - 445px);
    right: 298px;
}

.right-filter {
  right: 43px;
}

/*responsive css start*/
@media screen and (max-width: 767px) {

  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: inline-block;
  }

  ::ng-deep .modal-dialog {
    padding: 0 15px;
  }

  .ipadfiler button {
    text-align: right;
  }

  .filtericon {
    padding-right: 5px;
  }

  .qicon {
    display: none;
  }

  .qpart.qicon {
    display: initial;
  }

  .right-filter {
    right: 0;
  }

  .left-block {
    width: inherit !important;
    right: 0 !important;
  }

  .title-card {
    display: none;
  }

  .page-title {
    padding: 0;
    font-size: 17px;
  }

  .m-card h5 {
    display: block;
  }

  table {
    width: 100%;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  tr {
    border: none
  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td span.textTtl {
    display: inline-block !important;
    position: absolute;
    top: 50%;
    left: 25px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    transform: translate(0, -50%);
    font-weight: bold;
    color: #000;
  }

  table tr {
    height: 100%;
    margin-bottom: 25px;
  }

  table tr {
    border: none;
    margin-bottom: 20px;
    border-radius: 10px;
    width: 100%;
  }

  .example-sidenav-content {
    background: #ececef;
  }

  .mat-table {
    background: transparent;
  }

  tr.mat-header-row {
    height: 0 !important;
  }

  table tr th {
    display: none;
  }

  .btn-toggle:after {
    width: 5rem;
  }

  table tr td {
    width: 100% !important;
    padding-left: 50% !important;
    text-align: left !important;
    height: 45px;
    line-height: 12px;
    padding-top: 15px;
    padding-right: 15px;
    border: none;
  }

  table tr td:last-child {
    padding-top: 5px;
  }

  .example-container tr td:last-child {
    max-width: 100%;
  }

  .mat-table,
  .dashboard .mat-card {
    background: transparent;
  }

  .toprow {
    background: #fff;
    box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
    padding-bottom: 0.5rem !important;
    margin-bottom: 15px;
    border-bottom: 2px solid #ccc;
    border-top: 2px solid #ccc;
    padding-left: 20% !important;
  }

  .example-container1 {
    margin-top: 0;
  }

  .dashboard-wrapper {
    padding-left: 0px !important;
    padding-right: 0px !important;
  }

  .norecord {
    background-color: #ffffff !important;
    border-radius: 10px !important;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
  }

  .norecord tr td {
    width: 100% !important;
    text-align: center;
    border: none;
  }

  .sidefilter .head1 {
    padding-left: 23%;
  }

  .norecord td::before {
    display: none;
  }

  .norecord td {
    padding-left: 0 !important;
  }

  .example-container {
    height: calc(100vh - 100px);
    ;
    padding: 12px 12px 0 12px;
  }

  .patient-table td.mat-cell:first-of-type,
  .patient-table td.mat-footer-cell:first-of-type {
    font-family: "gothambook", sans-serif !important;
  }
}

@media screen and (min-width: 768px) and (max-width:1024px) {
  .example-container {
    overflow: scroll;
  }

  .qpart.qicon {
    display: none;
  }

  .ipadfiler button {
    text-align: right;
  }

  .left-block {
    width: inherit !important;
    right: 0 !important;
  }
}

.left-block {
  height: auto !important;
}

